@tailwind base;
@tailwind components;
@tailwind utilities;

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "Poppins", "Times New Roman", Times, serif;
}

body {
  width: 100%;
  height: 100vh;
}

html {
  width: 100%;
  height: 100vh;
  overflow: auto;
}

#root {
  width: 100%;
  height: 100%;
}

.footer {
  width: 100%;
  height: 40px;
  position: fixed;
  bottom: 0;
}

.modal-main {
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.4);
  position: fixed;
  top: 0;
  z-index: 10;
  backdrop-filter: blur(1px);
}

.modal {
  background-color: #fff;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 400px;
  max-height: 600px;
  padding: 10px 40px;
  border: 1px solid rgba(0, 0, 0, 0.2);
  border-radius: 8px;
  overflow-y: auto;
  transition: all 1s;
  z-index: 1;
}

