/* Data Table CSS */

.data-table {
  width: 100%;
  border-collapse: collapse;
  padding: 10px 20px;
  margin-top: 10px;
}

thead {
  background-color: navy;
  color: #fff;
}

tbody {
  text-align: center;
}

th,
td {
  border: 2px solid rgba(0, 0, 0, 0.1);
  padding: 5px 10px;
}

tbody tr:nth-child(even) {
  background-color: rgba(0, 0, 0, 0.1);
}

tbody tr:hover {
  background-color: rgba(0, 0, 0, 0.1);
}

tbody td:last-child div {
  display: flex;
  justify-content: center;
  gap: 20px;
  font-size: 18px;
}

/* Mobile CSS */
@media screen and (max-width: 600px) {
  html {
    width: 600px;
    overflow: auto;
    height: 100vh;
  }

  body {
    width: 100%;
    height: 100%;
  }
  #root {
    width: 100%;
    height: 100%;
  }

  nav {
    width: 100%;
    padding: 0;
  }

  .login-img {
    display: none;
  }

  .transaction-div,
  .value-div {
    display: flex;
    flex-direction: column;
    align-items: center;
    overflow: auto;
  }
}
